.footer p {
  font-size: 12px;
  color: #6c7279; }

.footer a {
  color: #000; }

.footer a:hover {
  text-decoration: underline; }

.footer .footer-share {
  margin-top: 0; }

.footer .footer-share li {
  display: inline-block;
  float: none; }

.footer .footer-share a {
  border: none;
  font-size: 14px;
  color: #000;
  letter-spacing: 15px; }

.footer .fa-heart {
  color: #e84545;
  font-size: 12px;
  margin: 0 2px; }
